.imageview-wrap {
  font-size: 0; }
  .imageview-wrap .img-type-normal {
    width: 32%;
    margin-right: 2%;
    padding-top: 32%;
    display: inline-block;
    margin-bottom: 2%;
    background-size: cover;
    background-position: center center;
    background-color: #ccc; }
    .imageview-wrap .img-type-normal:nth-child(3n) {
      margin-right: 0; }
    .imageview-wrap .img-type-normal:nth-child(n + 10) {
      display: none; }
  .imageview-wrap .img-type-one {
    width: auto;
    height: auto;
    max-width: 70%;
    min-width: 30%;
    max-height: 100px; }
  .imageview-wrap .img-type-two:nth-child(2) {
    margin-right: 30%; }
  .imageview-wrap .img-type-two:nth-child(3) {
    margin-right: 2%; }



