.friends-header {
  width: 100%;
  height: 300px;
  position: relative;
  background: #ccc center center;
  background-size: cover;
  margin-bottom: 43px; }
  .friends-header .avatar-wrap {
    position: absolute;
    right: 15px;
    bottom: 0;
    -webkit-transform: translate(0, 50%);
        -ms-transform: translate(0, 50%);
            transform: translate(0, 50%); }
  .friends-header .people-nickname {
    font-size: 16px;
    color: #fff;
    padding: 15px 10px 0;
    display: inline-block; }
  .friends-header .avatar-img-wrap {
    border: .5px solid #ccc;
    float: right;
    font-size: 0; }
  .friends-header .avatar-img {
    background-color: #ccc;
    width: 80px;
    height: 80px;
    border: 2px solid #fff; }



