.news-review {
  background: #efefef;
  padding: 4px;
  border-radius: 2px;
  position: relative;
  margin-top: 4px;
  line-height: 18px; }
  .news-review .likes-wrap {
    color: #49618e;
    border-bottom: 1px solid #d5d5d5;
    padding: 3px;
    margin: 0 -3px 2px; }
    .news-review .likes-wrap .iconfont {
      margin-right: 5px; }
    .news-review .likes-wrap.no-border-bottom {
      border-bottom: none; }
  .news-review .arrow {
    position: absolute;
    left: 10px;
    bottom: 100%;
    width: 0;
    height: 0;
    line-height: 0;
    font-size: 0;
    overflow: hidden;
    border-width: 8px;
    cursor: pointer;
    border-style: dashed dashed solid dashed;
    border-color: transparent transparent #efefef transparent;
    border-top: none;
    border-bottom-width: 6px; }
  .news-review .answer,
  .news-review .receive {
    color: #226792; }



