.friends-news {
  padding: 8px 15px 15px;
  border-bottom: 1px solid #d5d5d5;
  overflow: hidden; }
  .friends-news:last-child {
    border-bottom: none; }
  .friends-news-avator {
    background-color: #ccc;
    width: 40px;
    height: 40px;
    float: left; }
  .friends-news-container {
    overflow: hidden;
    padding-left: 8px;
    font-size: 12px;
    color: #8e8e8e; }
    .friends-news-container .news-name,
    .friends-news-container .new-address {
      color: #49618e;
      line-height: 1; }
    .friends-news-container .new-container {
      color: #333;
      margin: 5px 0; }
    .friends-news-container .news-from {
      margin-left: 10px; }
    .friends-news-container .other-wrap {
      margin-top: 4px;
      height: 26px;
      line-height: 26px;
      color: #818181; }
    .friends-news-container .control-wrap {
      float: right;
      position: relative; }
    .friends-news-container .control-btn {
      float: right; }
    .friends-news-container .control-like, .friends-news-container .control-review {
      width: 70px;
      display: inline-block;
      position: relative; }
    .friends-news-container .control-review:before {
      background: #fff;
      width: 1px;
      position: absolute;
      left: 0;
      top: 3px;
      content: '';
      height: 20px; }
    .friends-news-container .control-container {
      position: absolute;
      height: 26px;
      right: 110%;
      background-color: rgba(0, 0, 0, 0.8);
      color: #fff;
      border-radius: 1px;
      text-align: center;
      width: 160px;
      -webkit-transition: .5s all;
      -o-transition: .5s all;
      transition: .5s all;
      -webkit-transform: scale(0, 1);
          -ms-transform: scale(0, 1);
              transform: scale(0, 1);
      -webkit-transform-origin: right center;
          -ms-transform-origin: right center;
              transform-origin: right center; }
      .friends-news-container .control-container.show {
        -webkit-transform: scale(1, 1);
            -ms-transform: scale(1, 1);
                transform: scale(1, 1); }
      .friends-news-container .control-container .iconfont {
        padding: 0 7px; }



